const ClipButton = styled.button`
  grid-area: clip;
  color: inherit;
  padding: 0;
  border: none;
  outline: none;
  background: none;
  transition: opacity 0.3s ease-in-out;
  box-sizing: border-box;
  align-self: center;
  justify-self: end;
  height: 35px;
  ${(props) => !props.hasClip && `
  cursor: inherit;
  opacity: .3
  `};

  justify-self: end;
  ${(props) => props.hasClip && `
  &:hover {
    opacity: .6;
  }
  `};

  @media screen and (max-width: 767px) {
    justify-self: start;
  }

  @media screen and (max-width: 424px) {
    height: 100%;
    justify-self: start;

  }
`;

const ClickablePath = styled.path`
${(props) => (props.hasClip
    ? 'cursor: pointer;'
    : 'cursor: inherit;')
}
`;

const SvgButton = styled.svg`
  height: 100%;
`;

